.sr-only{position:absolute;width:1px;height:1px;padding:0;margin:-1px;overflow:hidden;clip:rect(0, 0, 0, 0);border:0
}
.sr-only-focusable:active,.sr-only-focusable:focus{position:static;width:auto;height:auto;margin:0;overflow:visible;clip:auto
}
html,body{height:100%;font-size:14px;color:#525252;font-family:NotoSansHans-Regular,AvenirNext-Regular,arial,Hiragino Sans GB,"Microsoft Yahei","Hiragino Sans GB","WenQuanYi Micro Hei",sans-serif;background:#f0f2f5
}
.navbar-default{background-color:#fff
}
.search-input{background-image:url("/assets/images/icon-search.png");background-repeat:no-repeat;background-position:10px;padding-left:25px;border:1px solid #c6c6c6;-webkit-box-shadow:none;box-shadow:none;-webkit-transition:all 0.15s ease-in 0s;transition:all 0.15s ease-in 0s
}
.topic-list .label{position:inherit;font-size:12px;font-weight:normal
}
.topic-list a,.topic-list a:focus,.topic-list a:hover{color:#555
}
.topic-list .list-group-item{height:58px
}
.topic-list .media-heading{white-space:nowrap;overflow:hidden
}
abbr[data-original-title],abbr[title]{cursor:none;border-bottom:none
}
a.stats{display:block;margin-top:10px;color:#888;margin-right:18px
}
a.stats i.fa{font-size:15px
}
a.stats:hover{text-decoration:none
}
.col-sm-6 a.stats{margin-right:0px
}
.deleted{color:#aaa;text-decoration:line-through;font-size:12px;text-align:center
}
.inline-block{display:inline-block
}
textarea.form-control{font-size:15px;line-height:30px
}
ul.list{margin:0;padding-left:11px
}
ul.list li{margin-bottom:4px;line-height:16px
}
.display-none{display:none
}
.red{color:red
}
.badge-important{background-color:#d15b47 !important
}
.badge-fade{background-color:#EBE8E8
}
.empty-block{text-align:center;line-height:60px;margin:10px;color:#ccc
}
.box{background-color:#fff;padding:10px;margin:0 0 20px 0;-webkit-box-shadow:0 .2em 0 0 #ddd,0 0 0 1px #ddd;box-shadow:0 .2em 0 0 #ddd,0 0 0 1px #ddd
}
.panel{border:1px solid #eaeaea
}
.loading{font-size:15px
}
.emoji{width:1.6em;display:inline-block;margin-bottom:0;margin-top:-5px;margin-left:5px
}
.divider{border-bottom:1px solid #ddd
}
.main-col{padding-left:0px
}
.side-bar{padding-left:0px;padding-right:0px
}
.left-col{padding-left:15px;padding-right:0px
}
.add-padding-vertically{padding-left:15px;padding-right:15px
}
.remove-padding-bottom{padding-bottom:0px
}
.remove-padding-left{padding-left:0px !important
}
.remove-padding-horizontal{padding-bottom:0px;padding-top:0px
}
.remove-padding-vertically{padding-left:0px;padding-right:0px
}
